.scrub-slider{position:relative;max-width:100%;max-height:100vw;height:500px;overflow:hidden}.scrub-slider:hover{cursor:pointer}.scrub-slider .scrub-content{display:block;position:absolute;top:0;overflow:hidden;height:100%;width:100vw}.scrub-slider .scrub-content.scrub-left{left:0}.scrub-slider .scrub-content.scrub-left>div{left:0}.scrub-slider .scrub-content.scrub-right{right:0}.scrub-slider .scrub-content.scrub-right>div{right:0}.scrub-slider .scrub-content>div{position:absolute;height:100%;width:100vw;background-repeat:no-repeat;background-position:center;background-size:cover}.scrub-slider .sliding{position:absolute;left:50%;height:100%;width:3px;z-index:2;background:#fff}.scrub-slider .sliding.handleOn:before{content:'';width:40px;padding-bottom:40px;position:absolute;display:block;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);border:3px solid #fff;border-radius:100%;-webkit-box-shadow:0 0 2px 1px #000;box-shadow:0 0 2px 1px #000;z-index:2}.scrub-slider .sliding.handleOn span[class*=sliding-]{width:0;height:0;border-top:10px solid transparent;border-bottom:10px solid transparent;position:absolute;top:50%}.scrub-slider .sliding.handleOn span[class*=sliding-][class*=left]{left:50%;-webkit-transform:translate(-150%,-50%);-ms-transform:translate(-150%,-50%);transform:translate(-150%,-50%);border-right:10px solid #000}.scrub-slider .sliding.handleOn span[class*=sliding-][class*=right]{right:50%;-webkit-transform:translate(140%,-50%);-ms-transform:translate(140%,-50%);transform:translate(140%,-50%);border-left:10px solid #000}
